
.nav-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 6.4rem;
  z-index: 10;
}
.nav-bar ul {
  margin: 0;
  padding: 0;
}
.nav-bar li {
  display: inline-block;
  width: 1.6rem;
  height: .8rem;
  background: #fafafa;
  border-top: 1px solid #ddd;
  text-align: center;
  transition: all .3s;
  cursor: pointer;
}
.nav-bar li.router-link-exact-active.router-link-active {
  background: orange;
  color: #eee;
}
.nav-bar li i {
  font-size: .4rem;
  margin-top: 0.1rem;
}



